header .inner {
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#logo {
  height: 100%;
  cursor: pointer;
  display: block;
  height: 100%;
  padding: 5px;
}
#logo > img {
  display: block;
  height: 100%;
  margin: auto;
  object-fit: contain;
  object-position: center;
}
#gnb > ul {
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#gnb > ul > li {
  display: block;
  padding: 1rem;
  transition: 550ms;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
#gnb > ul > li:hover {
  color: var(--point-color);
}
@media screen and (max-width: 767px) {
  header .inner {
    width: 100%;
    height: 70px;
    padding: 0 0.5rem;
    justify-content: space-between;
  }
  #logo {
    width: 70px;
    height: 70px;
    margin: 0;
  }
  #gnb {
  }
  #gnb > ul {
    width: auto;
  }
  #gnb > ul > li {
    padding: 0.5rem;
    font-weight: 400;
  }
  #gnb > ul > li:last-child {
    display: none;
  }
}
