@charset "UTF-8";
.aboutWrap {
  width: 100%;
  padding: 3rem 0;
  margin-bottom: 4rem;
}
.aboutWrap .inner {
  display: flex;
  justify-content: center;
}
.AboutTitle {
  display: block;
  width: 700px;
  padding: 1rem;
  border-radius: 60px;
  color: var(--point-bg);
  margin: 0 auto 3rem;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  background: #fff;
}
@media screen and (max-width: 767px) {
  .aboutWrap {
    width: 100%;
    padding: 2rem 0;
    margin: auto;
  }
  .AboutTitle {
    width: calc(100% - 1rem);
  }
  .aboutWrap .inner {
    flex-direction: column;
    padding: 0 0.5rem;
  }
}
